.massage {
 display: flex;
 margin: 0;
 padding: 0;
 background-color: chartreuse;
 align-items: flex-end;
}

.massage img{
 width: 50px;
 height: 50px;
 border-radius: 30px;
 display: flex;
 margin-bottom: 10px;
 margin-left: 10px;
}

.container {
 display: flex;
 background-color: bisque;
 border-radius: 4px 4px 4px 0;
 margin-bottom: 10px;
 margin-top: 10px;
}

.angel {
  width: 15px;
 height: 15px;
 background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, bisque 0);
 margin-bottom: 10px ;
}

.post {
 margin: 5px;
}

.post h3{
 margin: 0;
 padding: 0;
}

.post p {
 margin: 0;
 padding: 0;
}

.post span {
 float: right;
}


